import './style.css'
import * as THREE from 'three'
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import { GLTFLoader } from 'three/examples/jsm/loaders/GLTFLoader.js'
import { Sky } from 'three/examples/jsm/objects/Sky'


// Scene
const canvas = document.querySelector('canvas.scene')
const scene = new THREE.Scene()
const fogColor = new THREE.Color('#94A2B0')
fogColor.convertSRGBToLinear()
scene.fog = new THREE.FogExp2(fogColor, 0.01)

// Sky
const skyVals = {
    turbidity: 8,
	rayleigh: 0.0,
	mieCoefficient: 0.001,
    mieDirectionalG: 0.465,
    elevation:30,
    azimuth: -180,
}
let sky = new Sky()
sky.scale.setScalar(450000)
scene.add(sky)
let sun = new THREE.Vector3()
const uniforms = sky.material.uniforms
uniforms[ 'turbidity' ].value = skyVals.turbidity
uniforms[ 'rayleigh' ].value = skyVals.rayleigh
uniforms[ 'mieCoefficient' ].value = skyVals.mieCoefficient
uniforms[ 'mieDirectionalG' ].value = skyVals.mieDirectionalG
const phi = THREE.MathUtils.degToRad( 90 - skyVals.elevation )
const theta = THREE.MathUtils.degToRad( skyVals.azimuth )
sun.setFromSphericalCoords( 1, phi, theta )
uniforms[ 'sunPosition' ].value.copy( sun )
let ambLight = new THREE.AmbientLight(0xffffff, 0.1)
scene.add(ambLight)

const size = {
    width: window.innerWidth,
    height: window.innerHeight
}

// World
let dragonMixer = null

const gltf = new GLTFLoader()
gltf.load(
    'assets/models/base.glb',
    (base) => {
        dragonMixer = new THREE.AnimationMixer(base.scene)
        const dragonAction = dragonMixer.clipAction(base.animations[0])
        dragonAction.play()
        //scene.scale.set(50,50,50)
        scene.add(base.scene)
    }
)

// Window
window.addEventListener('resize', () =>
{
    // Update sizes
    size.width = window.innerWidth
    size.height = window.innerHeight

    // Update camera
    camera.aspect = size.width / size.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(size.width, size.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

// Base Camera
const camera = new THREE.PerspectiveCamera(55, size.width / size.height, 0.1, 5000)
camera.position.set(0,0,8)
scene.add(camera)

// Controls
const controls = new OrbitControls(camera, canvas)
controls.enableDamping = true
controls.target.set(0, 1, 0)
controls.enablePan = false
controls.enableZoom = false
controls.enableRotate = false

// Renderer
const renderer = new THREE.WebGLRenderer({ canvas: canvas, antialias: true })
renderer.setSize(size.width, size.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
renderer.outputEncoding = THREE.sRGBEncoding
renderer.physicallyCorrectLights
renderer.toneMapping = THREE.ACESFilmicToneMapping
renderer.toneMappingExposure = 1

// Shadows
renderer.shadowMap.enabled = true
renderer.shadowMap.type = THREE.PCFSoftShadowMap

// Render
const clock = new THREE.Clock()
let previousTime = 0

// Audio
const listener = new THREE.AudioListener()
camera.add( listener )
const sound = new THREE.Audio( listener )
const audioLoader = new THREE.AudioLoader()

document.getElementById("musicbtn").addEventListener("click", playMusic, false);
let count = 0
function playMusic() {
    count += 1
    if(count%2==1) {
        audioLoader.load( 'assets/audio/Ard_Skellig_Village.mp3', function( buffer ) {
            sound.setBuffer( buffer )
            sound.setLoop( true )
            sound.play()
        })
    } else {
        audioLoader.load( 'assets/audio/Ard_Skellig_Village.mp3', function( buffer ) {
            sound.setBuffer( buffer )
            sound.setLoop( true )
            sound.pause()
        })
    }
}

const tick = () => {
    let elapsedTime = clock.getElapsedTime()
    let deltaTime = elapsedTime - previousTime
    previousTime = elapsedTime

    if (dragonMixer) {dragonMixer.update(deltaTime)}

    // Camera Shake
    camera.position.x += Math.sin(elapsedTime) * 0.001
    camera.position.y += Math.cos(elapsedTime) * 0.001

    controls.update()
    renderer.render(scene, camera)
    window.requestAnimationFrame(tick)
}

tick()

let oldX = 0
let oldY = 0

window.onmousemove = (ev) => {
    let changeX = ev.x - oldX
    let changeY = ev.y - oldY

    camera.position.x -= changeX/5000
    camera.position.y -= changeY/5000

    oldX = ev.x
    oldY = ev.y
}